@import "https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap";
@import "https://fonts.googleapis.com/css2?family=Preahvihear&display=swap";
* {
  box-sizing: border-box;
  resize: none;
  scroll-behavior: smooth;
  outline: none;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
  font-weight: 400;
  text-decoration: none;
  list-style-type: none;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}

.header {
  z-index: 400;
  background-color: #1a0b2e;
  position: sticky;
  top: 0;
}

.header__content {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header__content__logo {
  max-width: 100px;
  min-width: 62px;
  padding: 20px;
}

.header__content__logo img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.header__content__list {
  max-width: 520px;
  justify-content: space-between;
  gap: 84px;
  display: flex;
}

.header__content__list a {
  color: #f1ecec;
  font-size: 15px;
}

.header__content__btn {
  min-width: 50px;
  min-height: 50px;
  cursor: pointer;
  z-index: 100;
  background-color: #1a0b2e;
  border-radius: 25px;
  justify-content: center;
  align-items: center;
  margin: 5px;
  padding: 15px;
  transition: all .5s ease-in-out;
  display: none;
  position: fixed;
  top: 0;
  right: 0;
}

.header__content__btn__burger {
  width: 40px;
  height: 2px;
  z-index: 100;
  background: #fff;
  border-radius: 5px;
  transition: all .5s ease-in-out;
  box-shadow: 0 2px 5px #381010;
}

.burger__wrapper {
  width: 300px;
  min-height: 500px;
  z-index: 5;
  visibility: hidden;
  opacity: 0;
  background: radial-gradient(100% 50%, #270b42 0%, #320f8500 100%);
  margin-right: -70px;
  transition: all .5s;
  position: fixed;
  top: 0;
  right: -100%;
}

.burger__wrapper__content {
  flex-direction: column;
  align-items: center;
  margin-top: 100px;
  display: flex;
}

.burger__wrapper__content a {
  color: #bcb9b9;
  margin: 10px;
}

.header__content__btn__burger:before, .header__content__btn__burger:after {
  content: "";
  width: 30px;
  height: 2px;
  z-index: 1;
  background: #fff;
  border-radius: 5px;
  transition: all .5s ease-in-out;
  position: absolute;
  box-shadow: 0 2px 5px #381010;
}

.header__content__btn__burger:before {
  transform: translateY(-16px);
}

.header__content__btn__burger:after {
  transform: translateY(16px);
}

.header__content__btn.open .header__content__btn__burger {
  box-shadow: none;
  background: none;
  transform: translateX(-50px);
}

.header__content__btn.open .header__content__btn__burger:before {
  transform: rotate(45deg)translate(35px, -35px);
}

.header__content__btn.open .header__content__btn__burger:after {
  transform: rotate(-45deg)translate(35px, 35px);
}

@media (max-width: 2000px) {
  .header__content__burger {
    display: none;
  }
}

@media (max-width: 850px) {
  .header__content__list {
    display: none;
  }

  .header__content__btn {
    display: flex;
  }

  .header__content__burger {
    display: block;
  }
}

