.scroller {
  height: 1.2em;
  width: 15em;
  margin-top: 30px;
  font-size: 1.4em;
  line-height: 1.3em;
  position: relative;
  overflow: hidden;
}

.scroller > p {
  animation: 5s infinite slide;
  position: absolute;
  top: 0;
}

.scroller br {
  text-align: center;
}

@keyframes slide {
  0% {
    top: 0;
  }

  25% {
    top: -1.4em;
  }

  50% {
    top: -2.9em;
  }

  75% {
    top: -4em;
  }
}

