  background-color: #150e20;
  margin: 0 auto;
  padding: 20px 0;
  position: relative;
}

.skills__wrapper {
  font-size: 20px;
}

.skills__wrapper__title {
  height: 61px;
  color: #f1ecec;
  background: url("Ellipse.dd845c72.svg") no-repeat;
  margin-bottom: 20px;
  padding: 5px;
  font-family: Preahvihear, sans-serif;
}

.skills__wrapper__list {
  grid-template-rows: repeat(1, minmax(200px, auto));
  grid-template-columns: repeat(auto-fit, minmax(250px, auto));
  grid-auto-rows: minmax(200px, auto);
  justify-content: center;
  gap: 50px;
  padding: 50px;
  display: grid;
}

.skills__wrapper__list__item {
  max-width: 250px;
  min-height: 250px;
  background-color: #50176a;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  animation: 2.8s infinite pulse;
  display: flex;
  box-shadow: 0 20px 30px #0003, inset 0 10px 30px 5px #fff;
}

.skills__wrapper__list__item:hover {
  color: #2e0622;
  background-color: #c535c8;
}

@keyframes pulse {
  0% {
    transform: scale(.95);
  }

  70% {
    transform: scale(1);
  }

  100% {
    transform: scale(.95);
  }
}

.pulse1 {
  animation-delay: 1s;
}

.pulse3 {
  animation-delay: 2s;
}

.pulse4 {
  animation-delay: 1.4s;
}

.pulse5 {
  animation-delay: 1.8s;
}

.pulse6 {
  animation-delay: 3s;
}

