  background-color: #150e20;
  margin: 0 auto;
  padding: 20px 0;
  position: relative;
}

.projects__wrapper {
  font-size: 20px;
}

.projects__wrapper__title {
  color: #f1ecec;
  margin-bottom: 20px;
  padding: 7px;
