}

.projects__wrapper__list {
  grid-template-rows: repeat(1, minmax(200px, auto));
  grid-template-columns: repeat(auto-fit, minmax(250px, auto));
  grid-auto-rows: minmax(200px, auto);
  justify-content: center;
  gap: 50px;
  padding: 50px;
  display: grid;
}

.projects__wrapper__list__item {
  max-width: 400px;
}

.projects__wrapper__list__item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 20px;
}

.projects__wrapper__list__description {
  max-width: 300px;
}

.projects__wrapper__list__description img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  border-radius: 20px;
}

.contacts {
  min-height: 500px;
  color: #f1ecec;
  background-color: #150e20;
  margin: 0 auto;
  padding: 20px 0;
}

.contacts__title {
  color: #f1ecec;
  justify-content: center;
  margin: 5px;
  display: flex;
}

.contacts__wrapper {
  max-width: 400px;
  background-color: #f1ecec;
  border-radius: 25px;
  margin: 0 auto;
}

.contacts__wrapper__form {
  max-width: 450px;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 15px;
  display: flex;
}

.contacts__wrapper__form_item {
  width: 100%;
  margin: 5px 0;
  position: relative;
}

.contacts__wrapper__form_item label {
  color: #221f1f;
  text-transform: capitalize;
  pointer-events: none;
  padding: 0 5px;
  font-size: 11px;
  transition: top .3s;
  position: absolute;
  top: 8px;
  left: 10px;
}

.contacts__wrapper__form_item input, .contacts__wrapper__form_item textarea {
  width: 100%;
  color: #454242;
  background-color: #f1ecec;
  border: 1px solid #b3a2a2;
  border-radius: 15px;
  padding: 5px 15px;
}

.contacts__wrapper__form_item input:focus ~ label, .contacts__wrapper__form_item input:valid ~ label, .contacts__wrapper__form_item textarea:focus ~ label, .contacts__wrapper__form_item textarea:valid ~ label {
  color: #8e187a;
  background-color: #f1ecec;
  top: -7px;
}

.contacts__wrapper__form__button {
  width: 60%;
  text-align: center;
  color: #fff;
  background-color: #450e4e;
  border: none;
  border-radius: 25px;
  margin: 20px auto 0;
  padding: 10px;
  font-size: 12px;
  font-weight: 700;
  line-height: 21px;
  transition: all .3s;
  box-shadow: 0 0 #000;
}

.form__input._error {
  box-shadow: 0 0 2px red;
}

.privacy-policy__input._error {
  border: 2px solid #a12424bf;
}

.privacy-policy {
  align-items: center;
  display: flex;
}

.privacy-policy__input {
  width: 1.3em;
  height: 1.3em;
  vertical-align: middle;
  appearance: none;
  cursor: pointer;
  background-color: #ddd;
  border: 1px solid #ddd;
  border-radius: 50%;
  outline: none;
  margin-left: 10px;
}

.privacy-policy__input:checked {
  background-color: #450e4e;
}

.privacy-policy p {
  color: #877777;
  margin: 0 15px;
  font-size: 12px;
}

.overlay {
  width: 100%;
  height: 100%;
  z-index: 500;
  background-color: #000000d0;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.pop-up {
  z-index: 10;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.pop-up__content {
  max-width: 800px;
  background: radial-gradient(80% 60%, #763cac 0%, #f4f4f492 100%);
  border-radius: 25px;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0 auto;
  padding: 50px;
  display: flex;
  position: relative;
  box-shadow: 0 0 10px #4d4d4d;
}

.pop-up__content__close {
  width: 35px;
  height: 35px;
  color: #000;
  cursor: pointer;
  background-color: #661972b3;
  border: none;
  border-radius: 25px;
  position: absolute;
  top: 20px;
  right: 20px;
}

.pop-up__content__image {
  max-width: 800px;
  min-width: 100px;
  max-height: 400px;
}

.pop-up__content__image img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.pop-up__content__info {
  max-width: 250px;
  padding: 20px;
}

.pop-up__content__info__description {
  color: #fff;
  text-align: center;
  font-size: 14px;
}

